"use client";

import { motion } from "framer-motion";
import { Calendar, MapPin, Ticket } from "lucide-react";
import { Button } from "@/components/ui/button";
import Link from "next/link";

export default function EventsPage() {
    // Mock data for now
    const events = [
        {
            id: 1,
            name: "Base Hackathon Pakistan",
            date: "Dec 15, 2025",
            location: "LUMS, Lahore",
            price: "0.01 ETH",
            image: "bg-blue-500",
        },
        {
            id: 2,
            name: "Web3 Summit 2025",
            date: "Jan 20, 2026",
            location: "NUST, Islamabad",
            price: "0.05 ETH",
            image: "bg-purple-500",
        },
        {
            id: 3,
            name: "Music Festival",
            date: "Feb 14, 2026",
            location: "Arts Council, Karachi",
            price: "0.02 ETH",
            image: "bg-pink-500",
        },
    ];

    return (
        <div className="container mx-auto py-12 px-4">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5 }}
                className="mb-12 text-center"
            >
                <h1 className="text-4xl font-bold mb-4">Upcoming Events</h1>
                <p className="text-muted-foreground">Discover and attend the best events in Pakistan.</p>
            </motion.div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {events.map((event, index) => (
                    <motion.div
                        key={event.id}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.5, delay: index * 0.1 }}
                        className="group relative bg-card border rounded-2xl overflow-hidden hover:shadow-xl transition-all duration-300 hover:-translate-y-1"
                    >
                        <div className={`h-48 ${event.image} relative overflow-hidden`}>
                            <div className="absolute inset-0 bg-black/20 group-hover:bg-black/10 transition-colors" />
                        </div>

                        <div className="p-6">
                            <h3 className="text-xl font-bold mb-2">{event.name}</h3>

                            <div className="space-y-2 mb-6">
                                <div className="flex items-center text-sm text-muted-foreground">
                                    <Calendar className="w-4 h-4 mr-2" />
                                    {event.date}
                                </div>
                                <div className="flex items-center text-sm text-muted-foreground">
                                    <MapPin className="w-4 h-4 mr-2" />
                                    {event.location}
                                </div>
                            </div>

                            <div className="flex items-center justify-between">
                                <span className="font-bold text-lg">{event.price}</span>
                                <Link href={`/events/${event.id}`}>
                                    <Button size="sm" className="rounded-full">
                                        Get Ticket <Ticket className="w-4 h-4 ml-2" />
                                    </Button>
                                </Link>
                            </div>
                        </div>
                    </motion.div>
                ))}
            </div>
        </div>
    );
}
